import React, {useState, useRef, useEffect} from 'react'
import {v4 as uuidv4} from 'uuid'
import './Todo.css'

const TodoIndex = () => {

    const [todos, setTodos] = useState([]);
    const todoRef = useRef();

    const addTodo = (e) => {
        e.preventDefault();
        const name = todoRef.current.value;
        if (!name) return
        setTodos([...todos, {name: name, id: uuidv4()}])
        todoRef.current.value = null
    }

    const removeTodo = (id) => {
        const removedTodo = todos.filter(todo=>todo.id !== id)
        setTodos(removedTodo)
    }

    useEffect(()=> {
        const todo = localStorage.getItem('todo')
        setTodos(JSON.parse(todo))
    }, [])

    useEffect(()=> {
        localStorage.setItem('todo', JSON.stringify(todos))
    }, [todos])

    return (
        <container className='container'>

            <h1 className='h1'>todos</h1>

            <form onSubmit={addTodo} className='form'>
                <input ref={todoRef} className='input'/>
                <button className='add-btn'>enter</button>
            </form>

            {todos.map(todo=>(
                <div key={todo.id} className='todos'>
                    {todo.name}<button onClick={()=>{removeTodo(todo.id)}} className='del-btn'>x</button>
                </div>
            ))}
      </container>
    )
}

export default TodoIndex
